@import "../../styles/globals.scss";
@import "../../styles/responsive.scss";

.backdrop {
  z-index: 1111;
  position: fixed;
  opacity: 1;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(33, 33, 33, 0.8);

  .experienceModalContainer {
    display: flex;
    flex-flow: column;
    max-width: 550px;
    width: 90%;
    margin: 10px;
    padding: 10px;
    border-left: solid 1px #222;
    background-color: black;
    border-radius: 5px;
    box-sizing: border-box;
    padding: 10px;

    button {
      background-color: #222;
      color: #ddd;
      border: none;
      padding: 10px;

      &:hover {
        background-color: #222;
        color: #ffcea7;
      }
    }

    .expItemLabel {
      padding: 10px 0;
      font-size: 0.8rem;
    }

    .expItemRole {
      padding-bottom: 5px;
      font-weight: 800;
      color: #ff9fcf;
      font-size: 1.6rem;
    }

    .expItemStack {
      font-size: 1rem;
      font-weight: 800;
      width: 90%;
      color: rgb(255, 254, 200);
    }

    .expItemLinkContainer {
      display: flex;
      justify-content: flex-end;


      .expItemURL {
        text-decoration: none;
        padding: 10px;
        font-size: small;

        &:hover {
          color: $yellow;
        }
      }

      .expItemGithub {
        text-decoration: none;
        padding: 10px;
        font-size: small;

        &:hover {
          color: $yellow;
        }
      }
    }

    .expItemDate {
      padding: 5px;
      margin: 2px;
      text-indent: 10px;
      font-style: italic;
      font-size: small;
      align-self: flex-end;
    }

    .expAboutList {
      padding: 0;
      list-style-type: circle;
      list-style-position: inside;
      list-style-color: white;
      font-size: 0.9rem;

      li.expAboutListItem {
        display: list-item;
        list-style-type: disc;
        padding: 5px 15px;
        color: #fff;

      }

    }
  }
}